"use client";
import { Zap, Crown, Target } from "lucide-react";

export default function SubscriptionBreakdown({ stats }: { stats: any }) {
  const plans = [
    { 
      name: "Free Tier", 
      count: stats?.free_count || 0, 
      revenue: stats?.free_revenue || 0, 
      color: "from-gray-500 to-gray-700",
      icon: <Target className="text-gray-400" size={20}/> 
    },
    { 
      name: "Premium", 
      count: stats?.premium_count || 0, 
      revenue: stats?.premium_revenue || 0, 
      color: "from-blue-500 to-indigo-600",
      icon: <Zap className="text-blue-400" size={20}/> 
    },
    { 
      name: "Diamond", 
      count: stats?.diamond_count || 0, 
      revenue: stats?.diamond_revenue || 0, 
      color: "from-purple-500 to-pink-600",
      icon: <Crown className="text-yellow-400" size={20}/> 
    },
  ];

  return (
    <div className="grid grid-cols-1 gap-4">
      {plans.map((plan) => (
        <div key={plan.name} className="relative overflow-hidden bg-gray-800/40 border border-gray-700 p-5 rounded-2xl flex items-center justify-between group hover:border-emerald-500/50 transition-all duration-300">
          <div className="flex items-center gap-4">
            <div className={`p-3 rounded-xl bg-gradient-to-br ${plan.color} bg-opacity-10`}>
              {plan.icon}
            </div>
            <div>
              <h4 className="text-white font-bold">{plan.name}</h4>
              <p className="text-gray-400 text-xs">{plan.count} Vendors Active</p>
            </div>
          </div>
          
          <div className="text-right">
            <p className="text-xs text-gray-500 uppercase font-semibold">Total Generated</p>
            <p className="text-lg font-black text-white">
              ₦{Number(plan.revenue).toLocaleString()}
            </p>
          </div>

          <div className="absolute bottom-0 left-0 h-1 bg-gradient-to-r from-transparent via-emerald-500 to-transparent opacity-0 group-hover:opacity-100 transition-opacity w-full" />
        </div>
      ))}
    </div>
  );
}